// "1.            .
// 2.  :
// - Modal -      
// - TaskForm -     
// 3.  Modal  TaskForm"

import { Modal } from '../Modal/Modal';
import { TaskForm } from '../TaskForm/TaskForm';

export const TaskModal = ({ date, category, onCloseModal, type, id }) => {
  return (
    <Modal onCloseModal={onCloseModal}>
      <TaskForm
        date={date}
        category={category}
        onCloseModal={onCloseModal}
        type={type}
        id={id}
      />
    </Modal>
  );
};
