// "1.                (To do | In progress | Done)
// 2.    
//  -   ,
//  -     ,      ."

export const ColumnHeadBar = ({ type }) => {
  const normalizeTitle = type => {
    switch (type) {
      case 'to-do':
        return 'To do';
      case 'in-progress':
        return 'In progress';
      case 'done':
        return 'Done';
      default:
        return 'Unknown';
    }
  };

  return (
    <div>
      <h2>{normalizeTitle(type)}</h2>
      <button type="button">+</button>
    </div>
  );
};
